@font-face {
  font-family: spotify-circular;
  src: url(./fonts/CircularSpUIv3T-Black.ttf);
}
body{
  font-family: spotify-circular;
    background-color: #404b5b;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;

    margin: 0px;
    padding: 0px;
    overflow: hidden;
    
}
#restartLevelButtonMainScreen{
  position: absolute;
  right: 5px; 
  z-index: -1px;
  transition-property: opacity;
  transition-duration: 1.3s;
}
.importantButton{
  padding: 20px 40px 20px 40px;
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
  outline: 0px;
  border: none;
  background-color: #ffea4f;
  min-width: 300px;
  border-radius: 10px;
  font-family: 'Open Sans',sans-serif;
  font-weight: bold;
  transition:background-color;
  transition-duration: 0.5s;
  cursor: pointer;
}
.importantButton:hover{
  background-color: #b4a531;
}
.importantButton:active{
  transition-duration: 0.05s;
  background-color: #968928;
}
.flip{
  transform: rotateY(180deg);
}
.disabled{
  pointer-events: none;
}
.invisible{
  opacity: 0;
  pointer-events: none;
}

@media screen and (min-width: 0px) and (max-width:1860px) {
  #restartLevelButtonMainScreen{
      position: relative;
      left: 50%;
      margin-bottom: 10px;
      transform: translate(-50%);
      font-size: 3vmin;
  }
}


@media screen and (min-height: 0px) and (max-height: 682px){
  #restartLevelButtonMainScreen{
    padding: 15px 30px 15px 30px;
    min-width: 250px;
  }
}
