#backContainer{
    display:grid;
    flex-wrap: wrap;
    justify-items: center;
    align-content: space-around;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    transform: rotateY(180deg); 
    background-color: #516582;
    border-radius: 10px;
    overflow: hidden;
}

.backContainerItem{
    position: relative;
    transition-property: left,opacity;
    transition-duration: 1s,0.7s;
    transition-timing-function: ease-in-out;
}

#backContainerButton{
    margin-top:0px;
    width: 90%;
    transition-property: left,opacity;
    transition-duration: 1s,0.7s;
    transition-timing-function: ease-in-out;
}

#levelFinalStatus{
    margin-top: -50px;
}

#levelFinalAttempts{
    font-size: 32px;
}

.finalLevelFailed{
    color: rgb(198, 17, 51);
}

.finalLevelCompleted{
    color:rgb(36, 201, 18);
}

@media screen and (max-width:638px)  {
    #backContainer{
        font-size: 60%;
    }
    #levelFinalAttempts{
        font-size: 120%;
    }
    #backContainerButton{
        font-size: 100%;
    }
}

@media screen and (max-width:1820px) and (min-width:639px){
    
    #backContainer{
        font-size: 20px;
    }
    #levelFinalAttempts{
        font-size: 20px;
    }
    #backContainerButton{
        font-size: 20px;
    }
}

