#frontContainer{
    display: grid;
    grid-template-rows: auto auto auto auto auto;
    grid-auto-flow: column;
    justify-content: center;
    align-content: center;
    gap: 10px;
    
    top: 0px;
    bottom: 0px;
    right: 0px;
    left: 0px;
}

@media screen and (min-height: 0px) and (max-height: 682px){
    #frontContainer{
        gap: 5px;
    }
}

@media screen and (min-height: 0px) and (max-height: 532px){
    #frontContainer{
        column-gap: 14px;
    }
}