#cardsContainer{
    transition: transform 2s; 
    transform-style: preserve-3d;
    color: white;
    font-size: 24px;
    width: fit-content;
    margin: auto;
}



#frontContainer,#backContainer{
    backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
}
