.card{
    width: 130px;
    height: 130px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.8s;
}
.card img{
    width: 96px;
}

.cardContentBack,.cardContentFront{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;  
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 5px 6px 10px rgba(204, 204, 204, 0.3);
    backface-visibility: hidden;
}
.cardContentFront{
    background-color: #0F1621;
    transform: rotateY(180deg);    
}
.cardContentBack{
    background-color: #00A2FF;
    color: black;
    font-size: 3vmin;
    cursor: pointer;
}



@media screen and (min-width: 0px) and (max-width:1860px) {
    .card{
        width: 12vmin; 
        height: 12vmin;
    }
    .card img{
        width: 8.88vmin;
    }
}

@media screen and (min-height: 0px) and (max-height: 532px){
    .card{
        width: 11.5vmin;
        height: 11.5vmin;
    }
    .card img{
        width: 8vmin;
    }
}
