#infoContainer{
    margin:auto;
    text-align: center;
    width: 70%;
    margin-bottom: 20px;
    margin-top: 20px;
    font-size: 20px;
    font-family: 'Open Sans', sans-serif;
    background-color: #222d3d;
    color: white;
    border-radius: 10px;
    padding: 10px;
    position: relative;
    transition-property: opacity;
    transition-duration: 1.3s;
}

@media screen and (min-width: 0px) and (max-width:1860px) {
    #infoContainer{
        margin-bottom: 5px;
        margin-top: 5px;
        font-size: 2vmin;
        padding: 5px;
    }
}